import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import './assets/css/reset.css'
import common from './utis/common'
Vue.use(ElementUI);
import vueSwiper from 'vue-awesome-swiper'
// import "../public/typeface/tf.scss"
Vue.use(vueSwiper)
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: localStorage.getItem('locale') || 'en', //  //this.$i18n.locale // locale
  messages: {
    'zh': require('./utis/language-zh'),  //
    'en': require('./utis/language-en')
  }
})
Vue.prototype.$common = common
Vue.config.productionTip = false
Vue.filter('addressFilter', function (value) {
  return String(value).replace(/(.{4})(.*)(.{4})/g, '$1...$3')
})

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
